import React, { useState } from 'react'
import { Button, Card, Col, Container, Form, Row } from 'react-bootstrap'
import { useSelector } from 'react-redux'
import { Link } from 'react-router-dom'

const Index = () => {
  const user = useSelector((state) => state.user.user)
  const [validated, setValidated] = useState(false)
  const [firstName, setFirstname] = useState('')
  const [lastName, setLastname] = useState('')
  const [mobile, setMobile] = useState('')
  const [email, setEmail] = useState('')
  const [message, setMessage] = useState('')
  const [complaintTitle, setComplaintTitle] = useState('')
  const [complaintImage, setComplaintImage] = useState('')

  const handleComplain = (event) => {
    try {
      const form = event.currentTarget
      if (form.checkValidity() === false) {
        event.preventDefault()
        event.stopPropagation()
      }
      setValidated(true)
    } catch (error) {
      console.log(error)
    } finally {
      setValidated(false)
    }
  }
  const convertToBase64 = (file) => {
    return new Promise((resolve, reject) => {
      const fileReader = new FileReader()
      fileReader.readAsDataURL(file)
      fileReader.onload = () => {
        resolve(fileReader.result)
        setComplaintImage(fileReader.result)
      }
      fileReader.onerror = (error) => {
        reject(error)
      }
    })
  }

  return (
    <>
      <Row className="text-center mb-2">
        <h1>Let&#39;s Connect With Us</h1>{' '}
      </Row>
      <Container fluid className="d-flex flex-column-reverse flex-md-row">
        <Container className="col-12 col-md-8 mt-3 mt-md-0">
          <Card>
            <Card.Header>
              <div className="">Complaint Form</div>
            </Card.Header>
            <Card.Body>
              <Form noValidate validated={validated} onSubmit={handleComplain} autoComplete="off">
                <Row className="mb-3">
                  <Form.Group
                    as={Col}
                    className="col-sm-12 col-lg-6 col-xs-12 col-md-6 col-12"
                    controlId="formName"
                  >
                    <Form.Label>Firts Name</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Enter First Name"
                      onChange={(e) => {
                        setFirstname(e.target.value)
                      }}
                      value={firstName}
                      required
                    />
                  </Form.Group>
                  <Form.Group
                    as={Col}
                    className="col-sm-12 col-lg-6 col-xs-12 col-md-6 col-12"
                    controlId="formName"
                  >
                    <Form.Label>Last Name</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Enter Last Name"
                      onChange={(e) => {
                        setLastname(e.target.value)
                      }}
                      value={lastName}
                      required
                    />
                  </Form.Group>
                </Row>
                <Row className="mb-3">
                  <Form.Group
                    as={Col}
                    className="col-sm-12 col-lg-6 col-xs-12 col-md-6 col-12"
                    controlId="formName"
                  >
                    <Form.Label>Unique Id</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Enter Mobile Number"
                      value={user.uniqueId}
                      disabled
                    />
                  </Form.Group>

                  <Form.Group
                    as={Col}
                    className="col-sm-12 col-lg-6 col-xs-12 col-md-6 col-12"
                    controlId="formName"
                  >
                    <Form.Label>Email</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Enter Email"
                      onChange={(e) => {
                        setEmail(e.target.value)
                      }}
                      value={email}
                      required
                    />
                  </Form.Group>
                </Row>
                <Row className="mb-3">
                  <Form.Group
                    as={Col}
                    className="col-sm-12 col-lg-6 col-xs-12 col-md-6 col-12"
                    controlId="formName"
                  >
                    <Form.Label>Mobile Number</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Enter Mobile Number"
                      onChange={(e) => {
                        setMobile(e.target.value)
                      }}
                      value={mobile}
                      required
                    />
                  </Form.Group>
                  <Form.Group
                    as={Col}
                    className="col-sm-12 col-lg-6 col-xs-12 col-md-6 col-12"
                    controlId="formName"
                  >
                    <Form.Label>
                      Add a ScreenShot <span className="text-danger">*Note if neccessary</span>
                    </Form.Label>

                    <Form.Control
                      type="file"
                      placeholder="Enter Mobile Number"
                      onChange={(e) => {
                        convertToBase64(e.target.files[0])
                      }}
                      value={mobile}
                      required
                    />
                  </Form.Group>
                </Row>
                <div className="mb-3 d-flex flex-column-reverse flex-md-row">
                  <Form.Group
                    as={Col}
                    className="col-sm-12 col-lg-6 col-xs-12 col-md-6 col-12"
                    controlId="formName"
                  >
                    <Form.Label>Complaint Title</Form.Label>
                    <Form.Control
                      type="text"
                      placeholder="Enter Complaint Title"
                      onChange={(e) => {
                        setComplaintTitle(e.target.value)
                      }}
                      value={complaintTitle}
                      required
                    />
                  </Form.Group>
                  <Container className="mb-3 mb-md-0">
                    <div className="d-flex">
                      {complaintImage && (
                        <>
                          <p className="me-3">Preview</p>
                          <div className="d-flex"
                          style={{
                            background: '#041215',
                          }}
                          >
                            <img
                              src={complaintImage}
                              style={{
                                height: '100px',
                                width: '100px',
                                objectFit: 'contain',
                              }}
                              alt=""
                            />
                          </div>
                        </>
                      )}
                    </div>
                  </Container>
                </div>
                <Row className="mb-3">
                  <Form.Group as={Col} className="" controlId="formName">
                    <Form.Label>Complaint Message</Form.Label>
                    <Form.Control
                      as="textarea"
                      rows={3}
                      placeholder="Enter Complaint Message"
                      onChange={(e) => {
                        setMessage(e.target.value)
                      }}
                      value={message}
                      required
                    />
                  </Form.Group>
                </Row>
                <div className="d-flex justify-content-end">
                  <Button className="myCustomBtn" type="submit">
                    Submit
                  </Button>
                </div>
              </Form>
            </Card.Body>
          </Card>
        </Container>
        <Container className="col-12 col-md-4">
          <Card>
            <Card.Header>Contact Details</Card.Header>
            <Container className="d-flex flex-column align-items-center justify-content-between">
              <table className="m-3">
                <tr>
                  <td>
                    <i
                      style={{ background: '#4175fc' }}
                      className="fa-solid fa-phone p-2 rounded-5 text-white"
                    ></i>
                  </td>
                  <td>
                    <Link
                      style={{
                        textDecoration: 'none',
                        whiteSpace: 'nowrap',
                      }}
                      className="text-black ms-3"
                      to="tel:+91 - 9588285510"
                    >
                      +91 - 9588285510
                    </Link>
                  </td>
                </tr>
                <br />
                <tr>
                  <td>
                    <i
                      style={{ background: '#4175fc' }}
                      className="fa-solid fa-envelope p-2 rounded-5 text-white"
                    ></i>
                  </td>

                  <td>
                    <Link
                      style={{
                        textDecoration: 'none',
                      }}
                      className="text-black"
                      to="mailto:care.adinaths@gmail.com"
                      onClick={() => {
                        window.location.href = 'mailto:care.adinaths@gmail.com'
                      }}
                    >
                      care.adinaths@gmail.com
                    </Link>
                  </td>
                </tr>
                <br />
                <tr>
                  <td>
                    {' '}
                    <i
                      style={{ background: '#4175fc', whiteSpace: 'nowrap' }}
                      className="fa-solid fa-location-dot p-2 rounded-5 text-white"
                    ></i>
                  </td>
                  <td>Near ICICI Bank Varda Sagwara Dungarpur Rajasthan 314029</td>
                </tr>
              </table>
            </Container>
          </Card>
        </Container>
      </Container>
    </>
  )
}
export default Index

